* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

#search-box:focus {
  outline: none;
  border-color: blue
}

.country-list {
  list-style: none;
}

.country {
  display: flex;
  align-items: center;
}

.wrap {
  display: flex;
  align-items: center;
}

.country-flag {
  display: block;
  margin-right: 10px;
  max-height: 70px;
}

.country-name__large {
  font-size: 40px;
  font-weight: 700;
  margin: 0;
}

.card-list {
  list-style: none;
  padding-left: 0;
}